import styles from "./Toggle.module.css"
import {useTheme} from '../hooks/ThemeCtx'
import { useState, useRef } from "react"

export default function Toggle() {
  const [hovered, setHovered] = useState(false)
  const [active, setActive] = useState(false)
  const {dark, setDark } = useTheme()
  const themeOptions = ['Dark', 'Light', 'System']
  const themeRef = useRef()


  function handleClick(item, i) {
    setActive(!active)
    switch (i) {
      case 0:
        setDark(true)
        setHovered(false)
        themeRef.current.innerText = item
        break
      case 1:
        setDark(false)
        setHovered(false)
        themeRef.current.innerText = item
        break
      case 2:
        const prefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
        if (prefersDark) {
          setDark(true)
          setHovered(false)
        } else {
          setDark(false)
          setHovered(false)
        }
        themeRef.current.innerText = item
        break
      default:
        break
    }
  }
  return (
    <div className={styles.container} 
    >

      <div className={styles.input} 
        onClick={() => {setActive(!active)}}
        style={{
          backgroundColor: dark ? '#ccc' : '#333',
          color: dark ? '#333' : '#ccc',
        }}
        onMouseEnter={() => setHovered(true)}
        onMouseLeave={() => setHovered(false)}
      >
        <div className={styles.moon}>
          {dark ?
            (<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill= {dark ? "#333" : '#ccc'} stroke="none" height='25'>
            <path d="M17.293 13.293A8 8 0 016.707 2.707a8.001 8.001 0 1010.586 10.586z" />
          </svg>) :
            (<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill={dark ? '#333' : '#ccc'} height='25'>
  <path fillRule="evenodd" d="M10 2a1 1 0 011 1v1a1 1 0 11-2 0V3a1 1 0 011-1zm4 8a4 4 0 11-8 0 4 4 0 018 0zm-.464 4.95l.707.707a1 1 0 001.414-1.414l-.707-.707a1 1 0 00-1.414 1.414zm2.12-10.607a1 1 0 010 1.414l-.706.707a1 1 0 11-1.414-1.414l.707-.707a1 1 0 011.414 0zM17 11a1 1 0 100-2h-1a1 1 0 100 2h1zm-7 4a1 1 0 011 1v1a1 1 0 11-2 0v-1a1 1 0 011-1zM5.05 6.464A1 1 0 106.465 5.05l-.708-.707a1 1 0 00-1.414 1.414l.707.707zm1.414 8.486l-.707.707a1 1 0 01-1.414-1.414l.707-.707a1 1 0 011.414 1.414zM4 11a1 1 0 100-2H3a1 1 0 000 2h1z" clipRule="evenodd" />
</svg>)}
        </div>
        <div className={styles.inputtext}
          style={{
            width: hovered ? '3rem' : 0,
            margin: hovered ? 1 : 0,
            padding: hovered ? 1 : 0,
          }}
        >
          <p ref={ themeRef }>Dark</p>
          <p>Theme</p>
        </div>
      </div>

      <div className={styles.options} 
        style={{
          visibility: active ? 'visible' : 'hidden',
        }}
        onMouseEnter={() => setHovered(true)}
        onMouseLeave={() => setHovered(false)}
      >
        <ul>
    {themeOptions.map((item, i) => (
      <li key={item}
      onClick={ () => handleClick(item, i)}
      >
      {item}</li>
    ))}
        </ul>
      </div>

    </div>
  )
}

